@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    font-family: 'Roboto', sans-serif;
}

.contacts {
    display: flex;
    flex-flow: column nowrap;
    width: 300px;
    margin: 0 auto;
    border: 5px solid rgba(126, 126, 126, 0.779);
    border-radius: 0 15px 0 15px;
    padding: 5px;
}

.search-field {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;

}

.search-field img {
    width: 16px;
    height: 16px;
    margin: 0 5px 0 5px
}

.search-field input {
    width: 80%;
    font-weight: 300;
}

.gender-filter {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    margin: 10px 0 20px 0;
    font-size: 15px; 
    text-transform: uppercase;
}

.gender-filter div {
    margin-right: 10px;
}


@media (max-width: 350px) {
    .contacts {
        width: 220px;
    }
}
.contact-item {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    margin: 15px 0 15px 5px;
}

.contact-item img, .contact-item div {
    margin: 0 5px 0 5px;
}

.contact-icon {
    width: 35px;
    height: 35px;
}

.contact-gender {
    width: 12px;
    height: 12px;
}

.contact-info {
    width: 60%;
    display: flex;
    flex-flow: column nowrap;
    font-size: 14px;
}

.contact-info span:first-child {
    font-size: 15px;
    font-weight: 600;
}

.contact-call {
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

@media (max-width: 350px) {
    .contact-icon {
        width: 30px;
        height: 30px;
    }
    
    .contact-gender {
        width: 10px;
        height: 10px;
    }

    .contact-call {
        width: 20px;
        height: 20px;
    }

    .contact-info {
        width: 62%;
        font-size: 11px;
    }
    
    .contact-info span:first-child {
        font-size: 12px;
    }
}

